
















































export default {
	data() {
		return {
			get_id: '',
			accountDetail:[],
		}
	},
	computed: {

	},
	onLoad(e) {
		if (e.get_id) {
			this.get_id = e.get_id;
		} else {
			uni.navigateTo({
				url: '/pages/user/account/account'
			})
		}
	},
	onShow() {
		this.getAccountDetail();
	},
	onBackPress() {},
	methods: {
		getAccountDetail() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const get_id = this.get_id;
			try {
				this.$http.post(this.websiteUrl + '/api/api/getAccountDetail', {
					openid,
					sessionKey,
					get_id
				}, (res) => {
					if (res.data.code == 1) {
						this.accountDetail = res.data.result.accountDetail;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		}
	}
}
